import { newE2EPage } from '@stencil/core/testing';

describe('nylas-pagination', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<nylas-mailbox-pagination></nylas-mailbox-pagination>');

    const element = await page.find('nylas-mailbox-pagination');
    expect(element).toHaveClass('hydrated');
  });
});
